* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 11vh;
    background-color: #343a40;
    margin-bottom: 2%;
}

.logo {
    color: white;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.logo h4 {
    margin: 0;
}

nav ul.nav-links {
    display: flex;
    justify-content: space-around;
    width: 40%;
    margin: 0;
}

nav ul.nav-links li {
    list-style: none;
}

.burger {
    display: none;
}

.burger div {
    width: 25px;
    height: 3px;
    background-color: white;
    margin: 5px;
    border-radius: 10px;
}

@media screen and (max-width: 1024px) {
    nav ul.nav-links {
        display: flex;
        justify-content: space-around;
        width: 60%;
        margin: 0;
    }
}

@media screen and (max-width: 768px) {
    body {
        /* overflow-x: hidden; */
    }

    nav ul.nav-links {
        position: absolute;
        right: 0px;
        height: 92vh;
        top: 8vh;
        background-color: #343a40;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 50%;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }

    .nav ul.nav-links li {
        opacity: 0;
    }

    .burger {
        display: block;
        cursor: pointer;
    }
}

.nav-active {
    -webkit-transition: -webkit-transform 0.5s ease-in;
    transition: -webkit-transform 0.5s ease-in;
    transition: transform 0.5s ease-in;
    transition: transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}
